.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  height: 100%; }

.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  width: 100%;
  height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 22px;
  color: #000000;
  background-color: aqua; }

.page-header {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  width: 100%;
  min-height: 825px;
  background-color: #000000;
  background-image: url("../img/header-bg-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover; }
  @media (max-width: 1024px) {
    .page-header {
      background-color: #ffffff;
      background-image: url("../img/header-bg-tablet@1x.jpg");
      background-size: contain; } }
  @media (max-width: 768px) {
    .page-header {
      background-image: none; } }
  @media (max-width: 1024px) {
    .page-header--nojs .page-header__navigation, .page-header--opened .page-header__navigation {
      display: flex; }
    .page-header--nojs .page-header__toggle, .page-header--opened .page-header__toggle {
      background-color: inherit;
      background-image: url("../img/icon-close.svg"); } }
  @media (max-width: 1024px) {
    .page-header--opened .page-header__navigation {
      display: flex; }
    .page-header--opened .page-header__toggle {
      background-color: inherit;
      background-image: url("../img/icon-close.svg"); } }
  @media (max-width: 1024px) {
    .page-header--closed .page-header__navigation {
      display: none; }
    .page-header--closed .page-header__toggle {
      background-color: #0ad9c6;
      background-image: url("../img/icon-burger.svg"); } }

.page-header__inner {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  max-width: 900px; }

.page-header__toggle {
  display: none;
  flex-wrap: wrap;
  width: 50px;
  height: 50px;
  border: none;
  background-color: #0ad9c6;
  background-image: url("../img/icon-burger.svg");
  background-repeat: no-repeat;
  background-size: 30px 30px;
  background-position: center;
  cursor: pointer; }
  @media (max-width: 1024px) {
    .page-header__toggle {
      display: flex;
      position: absolute;
      right: 0;
      z-index: 1; } }
  .page-header__toggle--open {
    background-color: inherit;
    background-image: url("../img/icon-close.svg"); }

.page-header__navigation {
  display: flex;
  flex-wrap: wrap; }
  @media (max-width: 1024px) {
    .page-header__navigation {
      display: none;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #ffffff;
      z-index: 1; } }

.page-header__description {
  display: flex;
  flex-wrap: wrap;
  width: 50%;
  color: #ffffff; }
  @media (max-width: 1024px) {
    .page-header__description {
      width: 100%;
      padding-top: 25px;
      margin-bottom: 19.5%; } }
  @media (max-width: 768px) {
    .page-header__description {
      align-content: flex-start;
      min-height: 390px;
      margin-bottom: 22%;
      background-color: #000000;
      background-image: url("../../img/header-bg-mobile@1x.jpg");
      background-repeat: no-repeat;
      background-size: cover; } }

.page-header__description svg {
  width: 67px;
  height: 20px;
  margin-left: 23px; }
  @media (max-width: 768px) {
    .page-header__description svg {
      margin-left: 18px; } }

.page-header__description h2 {
  display: flex;
  flex-wrap: wrap;
  margin-top: 28px;
  margin-bottom: 4px;
  font-style: normal;
  font-weight: bold;
  font-size: 46px;
  line-height: 64px;
  letter-spacing: -0.005em; }
  @media (max-width: 1024px) {
    .page-header__description h2 {
      justify-content: center;
      width: 100%;
      margin-top: 11.2%;
      font-size: 30px;
      line-height: 42px;
      margin-bottom: 18px; } }
  @media (max-width: 768px) {
    .page-header__description h2 {
      text-align: center;
      margin-top: 22.1%; } }

.page-header__description p {
  display: flex;
  flex-wrap: wrap;
  padding-right: 20px;
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  letter-spacing: -0.005em;
  mix-blend-mode: normal;
  opacity: 0.8; }
  @media (max-width: 1024px) {
    .page-header__description p {
      max-width: 435px;
      margin: 0 auto;
      padding-left: 20px;
      font-weight: 300;
      font-size: 18px;
      line-height: 32px;
      text-align: center; } }

.page-header__form {
  display: flex;
  flex-wrap: wrap;
  padding-left: 113px;
  padding-right: 10px; }
  @media (max-width: 1024px) {
    .page-header__form {
      padding: 0; } }

.navigation {
  width: 100%; }
  .navigation--hidden {
    display: none; }

.navigation__list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 0;
  padding: 0;
  padding-top: 20px;
  padding-bottom: 9px;
  margin-bottom: 256px;
  list-style: none; }
  .navigation__list::after {
    position: absolute;
    content: "";
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: rgba(255, 255, 255, 0.2); }
  @media (max-width: 1024px) {
    .navigation__list {
      align-items: flex-start;
      align-content: flex-start;
      padding-top: 100px; } }

.navigation__item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-right: 135px; }
  .navigation__item:nth-child(4n) {
    margin-right: 0; }
  @media (max-width: 1024px) {
    .navigation__item {
      width: 100%;
      background-color: #f6f6f6;
      margin-right: 0;
      margin-bottom: 30px;
      padding-left: 16px;
      padding-right: 40px;
      padding-top: 17px;
      padding-bottom: 17px; } }

.navigation__link {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
