      line-height: 28px;
      color: #48494d; } }

.form {
  align-content: flex-start;
  width: 50%; }
  @media (max-width: 1024px) {
    .form {
      min-width: 288px;
      max-width: 352px;
      margin: 0 auto;
      padding-left: 15px;
      padding-right: 15px; } }

.form__body {
  width: 100%; }

.form__title {
  margin: 0;
  margin-bottom: 20px;
  padding-top: 5px;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  text-align: center;
  letter-spacing: -0.005em;
  color: #ffffff; }
  @media (max-width: 1024px) {
