  flex-wrap: wrap;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none; }

.form__item {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin: 0;
  margin-bottom: 20px;
  padding: 0; }

.form input {
